* {
    margin: 0;
    padding: 0;
    box-sizing: inherit;


    // ? testing
    // border: 1px solid red;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;

    @include respond(media-sm) {
        font-size: 50%;
    }
}

body {
    box-sizing: border-box;
    font-weight: 400;
    font-size: $default-text-size;
    color: $grey;
    line-height: 1.7;
    background-image: $orange-gradient;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    @include respond(media-sm) {
        flex-direction: column;
        line-height: 1;
        padding: 2rem;
    }
}

::-webkit-scrollbar {
    display: none;
}

.primary-heading {
    color: $dirty-white;
    margin-bottom: 3rem;

    &--top {
        display: block;
        font-family: 'NGBold', sans-serif;
        font-size: 10rem;
        text-shadow: 0 1rem 3rem $shadow-4;

        @include respond(media-sm) {
            margin-bottom: 2rem;
            font-size: 8rem;
        }
    }

    &--sub {
        display: inline-block;
        font-size: 3.5rem;
        font-family: 'NGBoldItalic', sans-serif;
        font-weight: 100;
        background-color: $color-3;
        padding: 1rem 2rem;
        text-shadow: 0 0 1rem $shadow-4;
        box-shadow: 0 1rem 3rem $shadow-2;

        @include respond(media-sm) {
            font-size: 2.5rem;
        }
    }
}


.social-box {

    &__link {

        &:link,
        &:visited {
            color: $dirty-white;
            text-decoration: none;
            text-shadow: 0 0 1rem $shadow-4;
            transition: all .2s;

            &:not(:last-child) {
                margin-right: 2rem;
            }
        }

        &:hover {
            cursor: pointer;
            color: $text;
        }
    }

    &__icon {
        font-size: 5rem;
    }
}

.heading-box {
    position: relative;
}